@include keyframes (popup) {
    from {
        @include prefixer(transform, scale(0))
    }
    80% {
        @include prefixer(transform, scale(1.3))
    }
    to {
        @include prefixer(transform, scale(1))
    }
}

@include keyframes (slide-bg) {
    from {
        background-position: left;
    }
    50% {
        background-position: right;
    }
    to {
        background-position: left;
    }
}

@include keyframes (logo) {
    from {
        @include prefixer(transform, translate(-50%, -50%) scale(0))
    }
    15% {
        @include prefixer(transform, translate(-50%, -50%) scale(1.5))
    }
    50%,
    to {
        @include prefixer(transform, translate(-50%, -50%) scale(1))
    }
}

@include keyframes (circle2) {
    from {
        @include prefixer(transform, translate(-50%, -50%) scale(0))
    }
    16% {
        @include prefixer(transform, translate(-50%, -50%) scale(1))
    }
    20%,
    to {
        @include prefixer(transform, translate(-50%, -50%) scale(1))
    }
}

@include keyframes (circle1) {
    from {
        @include prefixer(transform, translate(-50%, -50%) scale(0))
    }
    35% {
        @include prefixer(transform, translate(-50%, -50%) scale(1.5))
    }
    to {
        @include prefixer(transform, translate(-50%, -50%) scale(1))
    }
}

@include keyframes (fa-spin) {
    from {
        // @include prefixer(transform, translate3d(-50%, -50%, 0));
        @include prefixer(transform, translate3d(-50%, -50%, 0) rotate(0deg));
    }
    to {
        @include prefixer(transform, translate3d(-50%, -50%, 0) rotate(359deg));
    }
}

@include keyframes (bg-gradient-translation) {
    @for $var from 0 to 51 {
        $percent: ( $var * 2) * 1%;
        #{$percent} {
            background: linear-gradient(to right, transparent calc(#{$percent}), $colorPrimary);
        }
    }
}

.no-scroll {
    overflow-y: hidden;
}