.project {
    &__button {
        &-container {
            display: flex;
            flex-flow: row wrap;
            justify-content: space-evenly;
            align-items: center;
            list-style: none;
            li {
                color: var(--text-color-primary);
                width: fit-content;
                min-width: 12em;
                justify-content: center;
                span {
                    width: 100%;
                }
            }
        }
    }
}