@include respond-to(small) {
    @media screen and (orientation: portrait), (min-width: $small) {
        // vertical bar
        nav {
            left: 100%;
            display: block;
            .menu-bar {
                background: $colorPrimary;
                height: 100vh;
                width: fit-content;
                right: 0;
                padding: 0.5rem;
                display: flex;
                flex-flow: column;
                justify-content: space-between;
                position: fixed;
                li {
                    display: flex;
                    justify-content: center;
                    list-style: none;
                    i {
                        font-size: 2rem;
                        color: #eee;
                    }
                }
                &__list-items {
                    li {
                        margin: 0.5rem;
                    }
                }
                &__trigger {
                    &-container {
                        display: flex;
                        flex-flow: row nowrap;
                        margin: 0.5rem;
                        transition: all 0.2s ease-in;
                        transition-delay: 0.5s;
                    }
                    &-bar {
                        display: block;
                        background-color: #eee;
                        height: 2rem;
                        width: 0.2rem;
                        margin: 0.15rem;
                    }
                    li {
                        display: flex;
                        align-items: center;
                        &:first-of-type {
                            &::before {
                                content: ' ';
                                display: block;
                                background-color: #eee;
                                height: 1rem;
                                width: 0.2rem;
                                margin: 0.1rem;
                            }
                        }
                        &:last-of-type {
                            &::after {
                                content: ' ';
                                display: block;
                                background-color: #eee;
                                height: 1rem;
                                width: 0.2rem;
                                margin: 0.1rem;
                            }
                        }
                    }
                    transition: all 0.5s ease-in;
                    &:hover {
                        transform: rotate(90deg);
                        ul {
                            transform: translateY(4rem);
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width:$small),
(min-width:$small) and (max-width:$medium) and (orientation: landscape) {
    nav {
        display: block;
        .menu-bar {
            background: $colorPrimary;
            height: fit-content;
            width: 100vw;
            top: 0;
            padding: 0.5rem;
            display: flex;
            flex-flow: row;
            justify-content: space-between;
            position: fixed;
            overflow: hidden;
            transition: all 0.5s ease-in-out;
            li {
                display: flex;
                justify-content: center;
                list-style: none;
                i {
                    font-size: 2rem;
                    color: #eee;
                }
            }
            &__list-items {
                ul {
                    display: flex;
                    flex-flow: row nowrap;
                    li {
                        margin: 0.5rem;
                    }
                }
            }
            &__trigger {
                &-container {
                    display: flex;
                    flex-flow: row nowrap;
                    margin: 0.5rem;
                    transition: all 0.2s ease-in;
                    transition-delay: 0.5s;
                }
                &-bar {
                    display: block;
                    background-color: #eee;
                    height: 2rem;
                    width: 0.2rem;
                    margin: 0.15rem;
                }
                li {
                    display: flex;
                    align-items: center;
                    &:first-of-type {
                        &::before {
                            content: ' ';
                            display: block;
                            background-color: #eee;
                            height: 1rem;
                            width: 0.2rem;
                            margin: 0.1rem;
                        }
                    }
                    &:last-of-type {
                        &::after {
                            content: ' ';
                            display: block;
                            background-color: #eee;
                            height: 1rem;
                            width: 0.2rem;
                            margin: 0.1rem;
                        }
                    }
                }
                transition: all 0.5s ease-in;
                &:hover {
                    ul {
                        transform: translateY(4rem);
                    }
                }
            }
        }
    }
}