header {
    @extend section;
    .header {
        &-list-button {
            &__container {
                list-style: none;
                max-width: 30rem;
                display: flex;
                justify-content: space-around;
                align-items: center;
                flex-flow: row wrap;
                margin: auto;
                @include respond-to(medium) {
                    max-width: 90%;
                }
                @include respond-to(xtra) {
                    max-width: 120rem;
                }
                li {
                    min-width: 30rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    &:not(:last-of-type) {
                        margin-bottom: 1.6rem;
                    }
                }
            }
        }
    }
}