.view-page {
    background-color: var(--bg-principal);
    // width: 100vw;
    &__container {
        margin: auto;
        width: calc(100% - 60px);
    }
}

@include respond-to(small) {
    @media screen and (orientation: portrait), (min-width: $small) {
        .view-page {
            transition: all 0.5s ease-in-out;
            &.anim {
                perspective: 1500px;
                background-color: #eee;
                .menu-bar {
                    animation: bg-gradient-translation 0.5s ease both;
                    height: 30vh;
                    top: calc(50% - 15vh);
                    transition-delay: 0s, 0.5s;
                    transition-property: height, background-color;
                    i {
                        transition: all 0.6s ease;
                        color: #333;
                    }
                    &__trigger {
                        transition: all 0.5s ease-in-out;
                        display: block;
                        &::before {
                            font: $font-family-fa;
                            content: "\f00d";
                            position: fixed;
                            width: fit-content;
                            font-size: 3.5em;
                            -moz-osx-font-smoothing: grayscale;
                            -webkit-font-smoothing: antialiased;
                            display: inline-block;
                            font-style: normal;
                            font-variant: normal;
                            text-rendering: auto;
                            line-height: 1;
                            border: #333 solid 1px;
                            display: block;
                            padding: 0 0.5rem;
                            right: 0.5rem;
                        }
                    }
                }
                .menu-list {
                    transition-delay: 0.5s;
                    transition: all 0.5s linear;
                    left: 45%;
                    @include respond-to(large) {
                        left: 50%;
                    }
                }
                .view-page__container {
                    outline: 3px dashed red;
                    overflow: hidden;
                    height: 100vh;
                    transition: all 0.5s ease-in;
                    transform-style: preserve-3d;
                    transform-origin: 50% 50%;
                    transform: translateX(-35%) rotateY(55deg) translateZ(-100px);
                    width: 100%;
                }
            }
            &__container {
                width: calc(100% - 4rem);
                margin: 0;
                header {
                    height: 50vh;
                    background-color: var(--bg-principal);
                }
            }
        }
    }
}