@include respond-to(small) {
    @media screen and (orientation: portrait), (min-width: $small) {
        nav {
            .menu-list {
                height: 100%;
                position: absolute;
                left: 100%;
                display: none;
                justify-content: center;
                flex-flow: column wrap;
                // margin-right: 4rem;
                width: calc(50% - 4rem);
                &__container {
                    list-style: none;
                    display: flex;
                    flex-flow: row wrap;
                }
                &__card {
                    // height: 34vh;
                    margin: 2rem;
                    padding: 1rem;
                    font-size: 2rem;
                    max-width: 60%;
                    &-list {
                        display: flex;
                        flex-flow: column nowrap;
                        justify-content: space-around;
                        height: 100%;
                        list-style: none;
                        align-items: center;
                        font-size: 1.3em;
                        position: relative;
                        &>li {
                            margin: 0 0 1.5em;
                            font-family: $font-family-headings;
                            text-align: center;
                            display: flex;
                            flex-flow: row nowrap;
                            @include respond-to(medium) {
                                margin: 0 0 3.5em;
                                @media screen and (orientation: portrait) {
                                    margin: 0 0 3.5em;
                                }
                            }
                            a {
                                &,
                                &:visited {
                                    text-decoration: none;
                                    text-transform: uppercase;
                                    letter-spacing: 0.4rem;
                                    transition: all 0.2s ease-in-out;
                                    width: 100%;
                                }
                                &:hover {
                                    color: $colorPrimary;
                                    &+ul {
                                        li {
                                            transform: rotateY(0) translateX(0);
                                            opacity: 1;
                                        }
                                    }
                                }
                                &+ul {
                                    list-style: none;
                                    position: absolute;
                                    left: 100%;
                                    text-align: start;
                                    width: fit-content;
                                    li {
                                        font-size: 0.7em;
                                        transition: all 0.5s linear;
                                        transform-origin: left center;
                                        transform: rotateY(90deg) translateX(-100px);
                                        transform-style: preserve-3d;
                                        opacity: 0;
                                        margin: 0 0 1em;
                                        &:nth-child(odd) {
                                            transition-delay: 0.2s;
                                        }
                                        &:first-child {
                                            margin-top: 1em;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}