.presentation {
    display: flex;
    flex-flow: column;
    .button {
        @include fluid-type(max-width, $small, $large, 30rem, 64rem);
        margin: auto;
        margin-bottom: 2rem;
    }
    p {
        color: var(--text-color-secondary);
        @include fluid-type(font-size, $small, $extra-large, 1.5rem, 2.1rem);
    }
}