.mainHome {
    font-family: $font-family-base;
    &__container {
        @media screen and (orientation: landscape) {
            max-width: 70%;
            margin: auto;
        }
        @include respond-to(small) {
            max-width: 80%;
            @media screen and (orientation: portrait), (min-width: $small) {
                max-width: 90%;
                margin: auto;
                // outline: solid red 1px;
            }
        }
        @include respond-to(xtra) {
            max-width: 130rem;
            margin: auto;
        }
    }
}