.portfolio {
    width: 80%;
    margin: 0 auto;
    &__title {
        font-size: 3vw;
        text-align: center;
        color: #eee;
        @media screen and (max-width: 816px) {
            font-size: 5.2vw;
        }
    }
    &__list {
        @supports (display: grid) {
            grid-column: full-start / full-end;
            display: grid;
            grid-template-columns: repeat(8, 1fr);
            grid-template-rows: repeat(6, 6vw);
            grid-template: repeat(6, 6vw)/repeat(8, 1fr);
            grid-gap: 0.7vw;
            padding: 1.5rem;
            @media screen and (max-width: 816px) {
                grid-template-columns: repeat(8, 1fr);
                grid-template-rows: repeat(36, 6vw);
                grid-template: repeat(36, 6vw)/repeat(8, 1fr);
                grid-gap: 4vw;
            }
        }
    }
    &__item {
        overflow: hidden;
        border-radius: 10px;
        transition: all 0.4s ease-in-out;
        box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.6);
        color: rgb(36, 113, 173);
        @media screen and (min-width: 816px) {
            &:hover {
                box-shadow: 1rem 1rem 1rem rgba(0, 0, 0, 0.3);
                transform: translate3d(-5px, -5px, 0);
            }
        }
        @media screen and (max-width:816px) {
            &:before {
                content: "click me";
                display: block;
                position: absolute;
                background: #eee;
                transform: rotate(-45deg) translate(-10%, -50%);
                z-index: 3;
                border-radius: 5px;
                padding: 1rem;
            }
        }
        &:hover .portfolio__item-container {
            bottom: 0;
            opacity: 1;
        }
        figure {
            background: red;
            position: relative;
            width: 100%;
            height: 100%;
            a {
                text-decoration: none;
                color: #444;
            }
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
        q {
            font-size: 1vw;
            color: #444;
            @media screen and (max-width: 816px) {
                font-size: 2.2vw;
            }
        }
        &--1 {
            grid-area: 4 / 1 /4 span /5 span;
            @media screen and (max-width: 816px) {
                grid-area: 1 / 1 /6 span /8 span;
            }
            ;
        }
        &--2 {
            grid-area: 1 / 1 / 3 span / 3 span;
            @media screen and (max-width: 816px) {
                grid-area: 7 / 1 /6 span /8 span;
            }
            ;
        }
        &--3 {
            grid-area: 1 /4 /3 span /2 span;
            @media screen and (max-width: 816px) {
                grid-area: 13 / 1 /6 span /8 span;
            }
            ;
        }
        &--4 {
            grid-area: 1 / 6 / 5 span / 3 span;
            @media screen and (max-width: 816px) {
                grid-area: 19 / 1 /6 span /8 span;
            }
            ;
        }
        &--5 {
            grid-area: 6 / 6 / 4 span / 3 span;
            @media screen and (max-width: 816px) {
                grid-area: 25 / 1 /6 span /8 Span;
            }
            ;
        }
        &--6 {
            grid-area: 8/1/2 span / 2 span;
            @media screen and (max-width: 816px) {
                grid-area: 31 / 1 /6 span /8 span;
            }
            ;
        }
        &--7 {
            grid-area: 8 /3 /2 span / 3 span;
            @media screen and (max-width: 816px) {
                grid-area: 37 / 1 /6 span /8 span;
            }
            ;
        }
        &-container {
            position: absolute;
            bottom: -100%;
            background: white;
            width: 100%;
            font-size: 1.2vw;
            line-height: 1.3vw;
            transition: all 0.4s ease;
            opacity: 0;
            @media screen and (max-width: 816px) {
                font-size: 3.2vw;
                line-height: 3.3vw;
            }
        }
        &-txt {
            margin: 0 1vw;
            padding: 0.5vw 0;
        }
        &-list {
            list-style: none;
            display: flex;
            justify-content: flex-end;
            margin: 0 1vw 0.5vw;
        }
        &-icon {
            margin: 1vw;
        }
    }
}