.form {
    padding: 2.5rem;
    @media screen and (min-width: $small) {
        width: 50%;
        padding: 5rem;
    }
    &__group:not(:last-child) {
        margin-bottom: 2rem;
    }
    &__group:last-child {
        margin: auto;
        button {
            width: 90%;
            background: var(--bg-form);
            color: var(--text-color-secondary);
            font-weight: $weight-bold;
            text-align: center;
            padding: 0;
            cursor: pointer;
            &:active {
                span {
                    @include shadowSkeuemorph('light', 'push');
                }
            }
            span {
                margin: auto;
                width: 100%;
                padding: 1em;
                &::before {
                    content: 'Envoyer →';
                }
            }
            &:disabled {
                color: white;
                span {
                    &::before {
                        content: 'En cour d\'envoie '
                    }
                }
                background: darkslateblue;
                cursor: wait;
            }
            &[value="send"] {
                color: white;
                border: lightgreen solid 2px;
                span {
                    &::before {
                        content: 'Envoyé'
                    }
                }
                background: green;
                cursor: default;
            }
            &[value="no-send"] {
                color: white;
                border: red solid 2px;
                span {
                    &::before {
                        content: 'Erreur'
                    }
                }
                background: red;
                cursor: not-allowed;
            }
        }
    }
    &__input {
        font-size: 1.5rem;
        font-family: inherit;
        color: inherit;
        padding: 1.5rem 2rem;
        border-radius: $borderRadius;
        background-color: rbga(var(--bg-principal), .5);
        border: none;
        border-bottom: 3px solid transparent;
        width: 100%;
        @media screen and (min-width: $small) {
            width: 90%;
        }
        display: block;
        transition: all .3s;
        @include shadowSkeuemorph('light', 'nopush');
        &:focus {
            outline: none;
            // box-shadow: 0 1rem 2rem rgba(#000, .1);
            border-bottom: 3px solid var(--color-primary);
        }
        &:focus:invalid {
            border-bottom: 3px solid red;
        }
        &::-webkit-input-placeholder {
            color: var(--text-color-secondary);
        }
        &:valid {
            border: lightgreen solid 1px;
        }
        &:focus:invalid {
            border: red solid 1px;
        }
    }
    &__label {
        font-size: 1.2rem;
        font-weight: 700;
        margin-left: 2rem;
        margin-top: .7rem;
        display: block;
        transition: all .3s;
    }
    &__input:placeholder-shown+&__label {
        opacity: 0;
        visibility: hidden;
        transform: translateY(-4rem);
    }
    &__radio-group {
        width: 49%;
        display: inline-block;
    }
    &__radio-input {
        display: none;
    }
    &__radio-label {
        font-size: $font-size-base;
        cursor: pointer;
        position: relative;
        padding-left: 4.5rem;
        display: flex;
        align-items: center;
        color: var(--text-color-secondary);
    }
    &__radio-button {
        height: 3rem;
        width: 3rem;
        border: 5px solid var(--color-primary);
        border-radius: 50%;
        display: inline-block;
        position: absolute;
        left: 0;
        &::after {
            content: "";
            display: block;
            height: 1.3rem;
            width: 1.3rem;
            border-radius: 50%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-color: $color-primary--bleu;
            opacity: 0;
            transition: opacity .2s;
        }
    }
    &__radio-input:checked~&__radio-label &__radio-button::after {
        opacity: 1;
    }
}

#formDest {
    display: none;
}

.contact__form {
    background-image: var(--bg-form);
    @media screen and (min-width: $small) {
        background-image: linear-gradient(105deg, var(--bg-form) 0%, var(--bg-form) 50%, transparent 50%), url('../Assets/imgs/bg-form.png');
    }
