[data-theme="dark"] {
    .form {
        &__group:last-child {
            button {
                &:active {
                    span {
                        @include shadowSkeuemorph('dark',
                        'push');
                    }
                }
            }
        }
        &__input {
            @include shadowSkeuemorph('dark',
            'nopush');
        }
    }
    .contact__form {
        @include shadowSkeuemorph('dark',
        'nopush');
    }
}