.project {
    &__item {
        width: 30rem;
        background: var(--bg-principal);
        @include shadowSkeuemorph('dark', 'nopush');
        border-radius: $borderRadius;
        overflow: hidden;
        margin: $blurShadow + 2px;
        transition: all 0.5s ease-in-out;
        &[data-type=realisation-pro] {
            .project__item {
                &--deco {
                    background: $skillBg--shalala;
                    ~div:last-child {
                        background: $skillBg--shalala;
                    }
                }
            }
        }
        &[data-type=projet-perso] {
            .project__item {
                &--deco {
                    background: $skillBg--earthlike;
                    ~div:last-child {
                        background: $skillBg--earthlike;
                    }
                }
            }
        }
        &[data-type=front-end] {
            .project__item {
                &--deco {
                    background: $skillBg--radar;
                    ~div:last-child {
                        background: $skillBg--radar;
                    }
                }
            }
        }
        &[data-type=back-end] {
            .project__item {
                &--deco {
                    background: $skillBg--celestial;
                    ~div:last-child {
                        background: $skillBg--celestial;
                    }
                }
            }
        }
        &[data-type=treeD] {
            .project__item {
                &--deco {
                    background: $skillBg--jaipur;
                    ~div:last-child {
                        background: $skillBg--jaipur;
                    }
                }
            }
        }
        &--deco {
            height: 3rem;
            width: 100%;
            border-top-left-radius: $borderRadius;
            border-top-right-radius: $borderRadius;
            &:last-child {
                height: 1.5rem;
                border-top-left-radius: 0;
                border-top-right-radius: 0;
                border-bottom-left-radius: $borderRadius;
                border-bottom-right-radius: $borderRadius;
            }
        }
        figure {
            img {
                object-fit: cover;
                width: 100%;
                height: 25rem;
            }
            figcaption {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-flow: column;
                color: var(--text-color-primary);
                h5 {
                    font-family: $font-family-headings;
                    margin-bottom: 0.5em;
                    font-size: 2.1rem;
                }
                p {
                    font-size: 1.5rem;
                    margin-bottom: 1em;
                }
            }
        }
        &:hover {
            z-index: 2;
            .project__item-container {
                @include shadowSkeuemorph('dark', 'push');
                align-items: center;
                &>*:not(.project__item--deco) {
                    transform-origin: center;
                    transform: scale(0.90);
                    z-index: -1;
                }
            }
        }
    }
    &__list {
        display: flex;
        justify-content: space-around;
        flex-flow: row wrap;
        align-items: center;
    }
}