    .skill {
        &__table {
            display: grid;
            grid-template-columns: repeat(var(--grid-column), 7.5rem);
            // grid-template-rows: repeat(var(--grid-row), 7.5rem);
            grid-gap: 32px;
            // grid-auto-flow: column;
            margin: auto;
            max-width: 1300px;
            justify-content: center;
            @media screen and (min-width: $large) {
                grid-template-columns: repeat(var(--grid-column), 12rem);
                grid-template-rows: repeat(var(--grid-row), 12rem);
                grid-gap: 20px;
                grid-auto-flow: column;
            }
            @media screen and (min-width: $xextra-large) {
                grid-template-columns: repeat(var(--grid-column), 15rem);
                grid-template-rows: repeat(var(--grid-row), 15rem);
                grid-gap: 20px;
            }
            &--soft {
                display: grid;
                grid-template-columns: repeat(var(--grid-column), 7.5rem);
                grid-gap: 32px;
                justify-content: center;
                margin: 5rem auto;
                @media screen and (min-width: $large) {
                    grid-template: repeat(2, 12rem) / repeat(var(--grid-column), 12rem);
                    grid-gap: 20px;
                }
                @media screen and (min-width: $xextra-large) {
                    grid-template: repeat(2, 15rem) / repeat(var(--grid-column), 15rem);
                }
            }
        }
        &__element {
            padding: 2px;
            transition: padding 0.5s ease-in-out;
            min-height: 7.5rem;
            &[data-mastered="false"] {
                display: none;
            }
            @media screen and (min-width:$large) {
                &[data-mastered="false"] {
                    display: block;
                }
            }
            @media screen and (min-width: $extra-large) {
                padding: 4px;
            }
            &:hover {
                padding: 3px;
                @media screen and (min-width: $extra-large) {
                    padding: 6px;
                }
            }
            @include shadowSkeuemorph('light', 'nopush');
            &--front {
                background: $skillBg--bleu;
            }
            &--back {
                background: $skillBg--vert;
            }
            &--crea {
                background: $skillBg--rose;
            }
            &--env {
                background: $skillBg--orange;
            }
            &--soft {
                background: $skillBg--fireWatch;
            }
            &-container {
                background: var(--bg-principal);
                display: flex;
                justify-content: center;
                flex-flow: column;
                align-items: center;
                width: 100%;
                // height: 100%;
                height: fill-available;
                padding: 4px;
                @supports not (height: fill-available) {
                    height: 100%;
                }
                transition: all 0.5s ease-in-out;
                @media screen and (min-width: $extra-large) {
                    padding: 8px;
                }
                &:hover {
                    @include shadowSkeuemorph('light', 'push');
                    padding: 5px;
                    @media screen and (min-width: $extra-large) {
                        padding: 10px;
                    }
                }
                figure {
                    margin: auto;
                    display: flex;
                    flex-flow: column;
                    img {
                        height: 2.6em;
                        max-width: 4.5em;
                        margin: auto;
                        margin-bottom: 2px;
                        @media screen and (min-width: $large) {
                            height: 5em;
                            max-width: 6em;
                            margin-bottom: 4px;
                        }
                        @media screen and (min-width: $extra-large) {
                            height: 6.4em;
                            max-width: 9em;
                            margin-bottom: 4px;
                        }
                    }
                    figcaption {
                        font-family: $font-family-headings;
                        text-align: center;
                        color: var(--text-color-primary);
                        @media screen and (min-width: $large) {
                            font-size: 1.5em;
                        }
                        @media screen and (min-width: $extra-large) {
                            font-size: 2em;
                        }
                    }
                }
            }
            &-info {
                display: flex;
                justify-content: space-between;
                width: 100%;
                align-items: center;
                span {
                    color: var(--text-color-primary);
                }
                svg {
                    max-height: 1.6em;
                    width: auto;
                }
            }
        }
        &__spacer {
            &--1 {
                grid-column: 2;
                grid-row: 1;
                @media screen and (max-width: $large) {
                    display: none;
                }
            }
            &--2 {
                grid-column: 1/ span 2;
                grid-row: 1/ span 2;
                @media screen and (min-width: 335px) {
                    grid-column: 1/ span 3;
                }
                @media screen and (min-width: $small) {
                    grid-column: 1/ span 4;
                }
                @media screen and (min-width: $medium) {
                    grid-column: 1/ span 5;
                }
                @media screen and (min-width:$large) {
                    grid-column: 2 /span 4;
                }
                @media screen and (min-width:$extra-large) {
                    grid-column: 3 /span 7;
                }
            }
            &--3 {
                grid-row: 1;
                grid-column: 7;
                @media screen and (max-width: $large) {
                    display: none;
                }
            }
        }
        &-info {
            list-style: none;
            display: flex;
            justify-content: center;
            flex-flow: column;
            align-items: center;
            height: 100%;
            color: var(--text-color-primary);
            @include fluid-type(font-size, $small, $large, 1.3rem, 2.6rem);
            span,
            svg {
                width: 1em;
                height: 1em;
                display: block;
                margin-right: 2em;
            }
            li {
                width: 80%;
                display: flex;
                flex-flow: row;
                margin: 0.2rem 0;
                align-items: center;
            }
            .no-mastered {
                display: none;
                @media screen and (min-width: $large) {
                    display: flex;
                }
            }
            .bg {
                &-front {
                    background: $skillBg--bleu;
                }
                &-back {
                    background: $skillBg--vert;
                }
                &-crea {
                    background: $skillBg--rose;
                }
                &-env {
                    background: $skillBg--orange;
                }
                &-other {
                    background: $skillBg--fireWatch;
                }
            }
        }
        &__container {
            display: flex;
            flex-flow: column;
            justify-content: center;
        }
    }
    
     :root[data-theme="dark"] {
        .skill {
            &__element {
                @include shadowSkeuemorph('dark', 'nopush');
                &-container {
                    &:hover {
                        @include shadowSkeuemorph('dark', 'push');
                    }
                }
            }
        }
    }