.timeline {
    position: relative;
    &::before {
        content: '';
        background: var(--bg-color-highlight);
        @include shadowSkeuemorph('light', 'nopush');
        width: 5px;
        height: 95%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
    }
}

.timeline-item {
    width: 100%;
    margin-bottom: 70px;
    &:nth-child(even) {
        .timeline-content {
            float: right;
            padding: 40px 30px 10px 30px;
            .date {
                right: auto;
                left: 0;
                border-top-right-radius: 0;
                border-top-left-radius: 8px;
            }
            .location {
                right: 0;
                left: auto;
                border-top-right-radius: 8px;
                border-top-left-radius: 0px;
            }
            &::after {
                content: '';
                position: absolute;
                border-style: solid;
                width: 0;
                height: 0;
                top: 30px;
                left: -15px;
                border-width: 10px 15px 10px 0;
                border-color: transparent #f5f5f5 transparent transparent;
            }
        }
    }
    &::after {
        content: '';
        display: block;
        clear: both;
    }
}

.timeline-content {
    position: relative;
    width: 45%;
    // padding: 10px 30px;
    padding: 40px 30px 10px 30px;
    border-radius: 10px;
    background: #f5f5f5;
    box-shadow: 0 20px 25px -15px rgba(0, 0, 0, .3);
    h2 {
        font-family: $font-family-headings;
    }
    &::after {
        content: '';
        position: absolute;
        border-style: solid;
        width: 0;
        height: 0;
        top: 30px;
        right: -15px;
        border-width: 10px 0 10px 15px;
        border-color: transparent transparent transparent #f5f5f5;
    }
}

.timeline-img {
    width: 30px;
    height: 30px;
    background: $color-primary--bleu;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    margin-top: 25px;
    margin-left: -15px;
}

.timeline-card {
    padding: 0 !important;
    p {
        padding: 0 20px 20px;
        @include fluid-type(font-size, $small, $extra-large, 1.5rem, 1.8rem);
    }
}

.timeline-item {
    .timeline-img-header {
        background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .4));
        background-size: cover;
        border-top: $borderRadius;
        img {
            object-fit: cover;
            width: 100%;
            height: 100%;
            border-top-left-radius: $borderRadius;
            border-top-right-radius: $borderRadius;
            filter: brightness(0.5);
        }
    }
    .button {
        @include fluid-type(font-size, $small, $extra-large, 1.5rem, 1.8rem);
        margin: 0 20px 1em;
        padding: 0;
        width: fit-content;
        a {
            padding: 1em;
        }
    }
}

.timeline-img-header {
    height: 200px;
    position: relative;
    margin-bottom: 20px;
    h2 {
        color: var(--text-color-primary);
        position: absolute;
        bottom: 5px;
        left: 20px;
        @include fluid-type(font-size, $small, $extra-large, 1.5rem, 2.1rem);
    }
}

.date {
    background: $color-primary--bleu;
    display: inline-block;
    color: var(--text-color-primary);
    padding: 10px;
    position: absolute;
    top: 0;
    right: 0;
    border-top-right-radius: 8px;
    @include fluid-type(font-size, $small, $extra-large, 1.1rem, 1.5rem);
}

.location {
    display: inline-block;
    color: var(--text-color-primary);
    padding: 10px;
    position: absolute;
    top: 0;
    left: 0;
    border-top-left-radius: 8px;
    background: $color-primary--bleu;
    @include fluid-type(font-size, $small, $extra-large, 1.1rem, 1.5rem);
}

@media screen and (max-width: 768px) {
    .timeline {
        &::before {
            left: 50px;
        }
        .timeline-img {
            left: 50px;
        }
        .timeline-content {
            max-width: 100%;
            width: auto;
            margin-left: 70px;
        }
        .timeline-item {
            &:nth-child(even) {
                .timeline-content {
                    float: none;
                }
            }
            &:nth-child(odd) {
                .timeline-content {
                    &::after {
                        content: '';
                        position: absolute;
                        border-style: solid;
                        width: 0;
                        height: 0;
                        top: 30px;
                        left: -15px;
                        border-width: 10px 15px 10px 0;
                        border-color: transparent #f5f5f5 transparent transparent;
                    }
                }
            }
        }
    }
}

[data-theme="dark"] .timeline {
    &::before {
        @include shadowSkeuemorph('dark', 'nopush');
    }
    &-item {
        width: 100%;
        margin-bottom: 70px;
        &:nth-child(even) {
            .timeline-content {
                float: right;
                padding: 40px 30px 10px 30px;
                .date {
                    right: auto;
                    left: 0;
                    border-top-right-radius: 0;
                    border-top-left-radius: 8px;
                }
                .location {
                    right: 0;
                    left: auto;
                    border-top-right-radius: 8px;
                    border-top-left-radius: 0px;
                }
                &::after {
                    content: '';
                    position: absolute;
                    border-style: solid;
                    width: 0;
                    height: 0;
                    top: 30px;
                    left: -15px;
                    border-width: 10px 15px 10px 0;
                    border-color: transparent #f5f5f5 transparent transparent;
                }
            }
        }
        &::after {
            content: '';
            display: block;
            clear: both;
        }
    }
}