:root[data-theme="dark"] {
    .card-id {
        @include shadowSkeuemorph('dark', 'nopush');
    }
}

.card-id {
    max-width: 90rem;
    overflow: hidden;
    margin: auto;
    @include shadowSkeuemorph('light', 'nopush');
    border-radius: $borderRadius;
    margin-bottom: 2rem;
    &__sign {
        position: relative;
        background: $color-primary--bleu;
        text-transform: uppercase;
        margin: 1em auto;
        max-width: 80%;
        width: 100%;
        @include respond-to(small) {
            max-width: 35%;
            margin-left: 0;
        }
        @include fluid-type(font-size, $small, $large, 1.5rem, 2.4rem);
        p {
            padding: 0.5em 0;
            color: var(--text-color-tertiary);
        }
        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 100%;
            left: 10%;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 1em 1em 0 0;
            border-color: $color-primary--bleu transparent transparent transparent;
        }
    }
    &__content {
        padding: 1.6em;
        display: flex;
        flex-flow: column;
        @include respond-to(small) {
            flex-flow: row;
        }
        &-image {
            max-width: 100%;
            flex: 1;
            // margin: auto;
            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
        }
        &-info {
            text-align: center;
            flex: 1;
            @include fluid-type(font-size, $small, $large, 1.4rem, 2.4rem);
            display: flex;
            justify-content: space-evenly;
            flex-flow: column;
            @include respond-to(small) {
                padding-left: 1.6rem;
                &-presentation {
                    text-align: left;
                }
            }
            &-name {
                margin-bottom: 1rem;
                span {
                    font-family: $font-family-headings;
                    font-weight: $weight-bold;
                    color: var(--text-color-primary);
                    @include fluid-type(font-size, $small, $large, 1.7rem, 2.7rem);
                }
            }
            &-role {
                margin-bottom: 1rem;
            }
        }
    }
    &__container {
        &--decoration {
            height: 2.6rem;
            background: $color-primary--bleu;
        }
    }
    &__detail {
        border-top: var(--text-color-primary) solid 2px;
        &-list {
            list-style: none;
            height: 100%;
            display: flex;
            flex-flow: column;
            li {
                text-align: left;
                font-family: $font-family-headings;
                color: var(--text-color-secondary);
                margin-top: 1rem;
                &:last-of-type {
                    margin-bottom: 0.5rem;
                }
                span {
                    &:first-child {
                        color: var(--text-color-primary);
                        font-weight: $weight-bold;
                    }
                }
            }
        }
    }
}