            /* tout les élements réutilisable*/
            
             :root[data-theme="dark"] {
                .button {
                    @include shadowSkeuemorph("dark", 'nopush', 6px);
                    a {
                        &:active {
                            @include shadowSkeuemorph('dark', 'push', 6px);
                        }
                    }
                }
            }
            
            .button {
                overflow: hidden;
                text-align: center;
                border-radius: $borderRadius * 2;
                width: 100%;
                display: inline-flex;
                justify-self: center;
                @include shadowSkeuemorph('light', 'nopush');
                transition: all 0.5s ease-in-out;
                a {
                    color: transparent;
                    text-decoration: none;
                    padding: 1em 0;
                    display: inline-block;
                    width: 100%;
                    font-family: #{$font-family-base};
                    font-weight: $weight-bold;
                    line-height: 1.3rem;
                    transition: all 0.2s ease-in-out;
                    @include fluid-type(font-size, $small, $extra-large, 1.5rem, 2.1rem);
                    &:active {
                        @include shadowSkeuemorph('light', 'push');
                    }
                }
                &--blue {
                    border: 2px #{$color-primary--bleu} solid;
                    a {
                        background: var(--text-color-primary);
                        background-clip: text;
                    }
                }
                &--lgshalala {
                    @include borderRadiusLinearGradient($skillBg--shalala);
                    a {
                        background: #{$skillBg--shalala};
                        background-clip: text;
                    }
                }
                &--lgcelestial {
                    @include borderRadiusLinearGradient($skillBg--celestial);
                    a {
                        background: #{$skillBg--celestial};
                        background-clip: text;
                    }
                }
                &--lgjaipur {
                    @include borderRadiusLinearGradient($skillBg--jaipur);
                    a {
                        background: #{$skillBg--jaipur};
                        background-clip: text;
                    }
                }
                &--lgradar {
                    @include borderRadiusLinearGradient($skillBg--radar);
                    a {
                        background: #{$skillBg--radar};
                        background-clip: text;
                    }
                }
                &--lgfireWatch {
                    @include borderRadiusLinearGradient($skillBg--fireWatch);
                    a {
                        background: #{$skillBg--fireWatch};
                        background-clip: text;
                    }
                }
                &--lgearthlike {
                    @include borderRadiusLinearGradient($skillBg--earthlike);
                    a {
                        background: #{$skillBg--earthlike};
                        background-clip: text;
                    }
                }
                &--realisation-pro {
                    @extend .button--lgshalala
                }
                &--projet-perso {
                    @extend .button--lgearthlike
                }
                &--front-end {
                    @extend .button--lgradar
                }
                &--back-end {
                    @extend .button--lgcelestial
                }
                &--3D {
                    @extend .button--lgjaipur
                }
            }